

.imageclass{
    height: 160px;
    width: 150px;
}

.quickSearchContainer{
    margin-top: 48px;
}

.quickSearchHeading{
    font-family: 'Poppins';
    font-size: 30px;
    font-weight: bold;
    color: #192f60;
    height: 43px;
}

.quickSearchSubHeading{
    margin-top: 7px;
    color: #8c96ab;
    font-family: 'Poppins';
    font-size: 18px;
    font-weight: normal;
    margin-bottom: 33px;
}

.tileContainer{
    height: 160px;
    box-shadow: 0 3px 6px 0px black;
    background-color: white;
    margin-bottom: 26px;
    float: left;
    margin-right: 30px;
    width: 360px;
}

.tileContainer:hover{
    transform: scale(1.1);
}

.tileComponent1{
    width: 40%;
    float: left;
}

.tileComponent2{
    width:60%;
    float: left;
    text-align: left;
    padding-left: 29px;

}

.componentHeading{
    font-size: 18px;
    font-weight: 600;
    font-family: 'Poppins';
    color: #192f60;
    margin-top: 25px;
    line-height: 1.5;
}

.componentInfo{
    font-size: 14px;
    font-family: 'Poppins';
    color: #8c96ab;
    margin-top: 4px;
}


@media screen and (min-width : 500px){
    .quickSearchContainer{
        margin-left: 18px;
    }
}