@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700;900&display=swap');
@import url('https://stackpath.bootstrapcdn.com/font-awesome/4.7.0/css/font-awesome.min.css');


.imageContainer{
    background-image: linear-gradient(to bottom, rgba(157, 14, 14, 0), #4f0707), url('main1.jpg');
    background-size: cover;
    width: 100%;
    height: 550;
    border-style: solid;
    border-color: white;
    border-width: 30px;
}

.logo_container{
    background-color: white;
    height: 115px;
    width: 115px;
    margin: auto;
    border-radius: 50%;
    margin-top: 81px;
    margin-bottom: 22px;
}

.logo{
    text-align: center;
    font-family: 'Poppins';
    font-size: 75px;
    font-weight: 600;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.51;
    letter-spacing: normal;
    color: #eb2929;
}

.heading{
        font-family: 'Poppins';
        font-size: 36px;
        font-weight: 600;
        text-align: center;
        color: white;
        
}

.locationSelector{
    text-align: center;
    margin-bottom: 81px;
    margin-top: 15px;

}

.locationDropDown{
    width: 250px;
    height: 50px;
    border-radius: 1%;
    font-size: 20px;
    border: none;
    padding-left: 10px;
    margin-right: 16px;
    text-align: center;
    padding-left: 35px;
}

.rest-info{
    width:400px;
    height: 50px;
    border-radius: 1%;
