.imageContainer{
    background-image: linear-gradient(to bottom, rgba(157, 14, 14, 0), #4f0707), url(/static/media/main1.c24d9fa7.jpg);
    background-size: cover;
    width: 100%;
    height: 550;
    border-style: solid;
    border-color: white;
    border-width: 30px;
}

.logo_container{
    background-color: white;
    height: 115px;
    width: 115px;
    margin: auto;
    border-radius: 50%;
    margin-top: 81px;
    margin-bottom: 22px;
}

.logo{
    text-align: center;
    font-family: 'Poppins';
    font-size: 75px;
    font-weight: 600;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.51;
    letter-spacing: normal;
    color: #eb2929;
}

.heading{
        font-family: 'Poppins';
        font-size: 36px;
        font-weight: 600;
        text-align: center;
        color: white;
        
}

.locationSelector{
    text-align: center;
    margin-bottom: 81px;
    margin-top: 15px;

}

.locationDropDown{
    width: 250px;
    height: 50px;
    border-radius: 1%;
    font-size: 20px;
    border: none;
    padding-left: 10px;
    margin-right: 16px;
    text-align: center;
    padding-left: 35px;
}

.rest-info{
    width:400px;
    height: 50px;
    border-radius: 1%;
    margin-top: 18px;
    border: none;
    padding-left: 50px;
    font-size: 20px;
    border: none;
}

.restnames{
    width: 330px;
    height: 50px;
    border-radius: 1%;
    font-size: 20px;
    border: none;
    padding-left: 10px;
    margin-right: 16px;
    text-align: center;
    padding-left: 35px;
}


.imageclass{
    height: 160px;
    width: 150px;
}

.quickSearchContainer{
    margin-top: 48px;
}

.quickSearchHeading{
    font-family: 'Poppins';
    font-size: 30px;
    font-weight: bold;
    color: #192f60;
    height: 43px;
}

.quickSearchSubHeading{
    margin-top: 7px;
    color: #8c96ab;
    font-family: 'Poppins';
    font-size: 18px;
    font-weight: normal;
    margin-bottom: 33px;
}

.tileContainer{
    height: 160px;
    box-shadow: 0 3px 6px 0px black;
    background-color: white;
    margin-bottom: 26px;
    float: left;
    margin-right: 30px;
    width: 360px;
}

.tileContainer:hover{
    transform: scale(1.1);
}

.tileComponent1{
    width: 40%;
    float: left;
}

.tileComponent2{
    width:60%;
    float: left;
    text-align: left;
    padding-left: 29px;

}

.componentHeading{
    font-size: 18px;
    font-weight: 600;
    font-family: 'Poppins';
    color: #192f60;
    margin-top: 25px;
    line-height: 1.5;
}

.componentInfo{
    font-size: 14px;
    font-family: 'Poppins';
    color: #8c96ab;
    margin-top: 4px;
}


@media screen and (min-width : 500px){
    .quickSearchContainer{
        margin-left: 18px;
    }
}
.item{
    border-radius: 10px;
    margin-bottom: 20px;
    background-image: linear-gradient(#ffffff,rgb(198, 198, 226));
    box-shadow: 3px 6px 8px #49639c;
    cursor: pointer;
}

.head-display{
    font-family: 'Piazzolla', serif;
    font-weight: 900;
    font-style: italic;
    font-size: 27px;
    color: rgb(21, 21, 94);
}

.image{
    width: 100%;
    height: 220px;
    border-radius: 20px;
    background-color: rgb(204, 201, 201);
    margin-left:5%;
    margin-top: 15px;
    margin-bottom: 2%;
    box-shadow: 1px 2px 3px black;
}

.row{
    margin-top: 10px;
}

.heads{
    font-family: 'Piazzolla', serif;
    font-weight: 700;
    margin-top: 15px;
    color: rgb(21, 21, 94);
    font-size: 30px;
}


.bottoms{
    font-family: 'Merriweather', serif;
    font-weight: 700;
    margin-top: 6px;
    font-size: 15px;
    color: rgb(38, 38, 99);
}

.hotel_name{
    margin-left: 5%;   
} 

.fa{
    color:rgb(38, 38, 99);
    margin-left: 5%;
    padding-top: 10px;
}

.cuisine_block {
    font-family: 'Merriweather', serif;
    font-size: 15px;
    color: #494c53;
    padding-left:10%;
    
}

.cuisine_out {
    font-family: 'Merriweather', serif;
    font-size: 15px;
    color: rgb(38, 38, 99);
}
.card{
    width: 211px;
    border-radius: 5%;
    background: #091446;
    color: #fff;
    margin-left:3%;
    float: left;
    height:250px;
    margin-top: 10px;
  }
  .card:hover { transform: scale(1.1); }
  .card-img-top{
    width:100%;
    height:80px;
  }
  
  .headingh1{
    text-align: center;
    color: rgb(255, 255, 255);
    text-shadow: 2px 2px #28346c;
  }
  
  .max{
    color: rgb(166, 226, 236);
    font-weight: bold;
  }
  .min{
    color:rgb(104, 155, 241);
    font-weight: bold;
  }
  .day{
    color: rgb(234, 255, 124);
  }
  .humid{
    color:rgb(44, 112, 105);
  }
  .topTemp{
    font-size:26px;
    color:white;
    font-weight: bold;
  }

  .card> h3> a{
      color:white;
      text-decoration: none;
      font-family: Verdana, Geneva, Tahoma, sans-serif;
      font-size: 21px;
  }

.head-top{
  font-family: 'Piazzolla', serif;
  font-weight: 900;
  font-style: italic;
  font-size: 26px;
  color: rgb(21, 21, 94);
  margin-left: 2%;
}
.panel > .panel-heading{
    background:rgb(21, 21, 94);
    color: white;
}

.titles{
    font-family: 'Piazzolla', serif;
    font-size: 30px;
}

.subTitle{
    font-family: 'Merriweather', serif;
    font-size: 19px;
}

p{
    padding-top: 5px;
