@import url('https://fonts.googleapis.com/css2?family=Merriweather:ital,wght@0,700;1,700&family=Piazzolla:ital,wght@0,700;0,800;1,600;1,700&display=swap');


.card{
    width: 211px;
    border-radius: 5%;
    background: #091446;
    color: #fff;
    margin-left:3%;
    float: left;
    height:250px;
    margin-top: 10px;
  }
  .card:hover { transform: scale(1.1); }
  .card-img-top{
    width:100%;
    height:80px;
  }
  
  .headingh1{
    text-align: center;
    color: rgb(255, 255, 255);
    text-shadow: 2px 2px #28346c;
  }
  
  .max{
    color: rgb(166, 226, 236);
    font-weight: bold;
  }
  .min{
    color:rgb(104, 155, 241);
    font-weight: bold;
  }
  .day{
    color: rgb(234, 255, 124);
  }
  .humid{
    color:rgb(44, 112, 105);
  }
  .topTemp{
    font-size:26px;
    color:white;
    font-weight: bold;
  }

  .card> h3> a{
      color:white;
      text-decoration: none;
      font-family: Verdana, Geneva, Tahoma, sans-serif;
      font-size: 21px;
  }

.head-top{
  font-family: 'Piazzolla', serif;
  font-weight: 900;
  font-style: italic;
  font-size: 26px;
  color: rgb(21, 21, 94);
  margin-left: 2%;
}