@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700;900&display=swap');
@import url('https://stackpath.bootstrapcdn.com/font-awesome/4.7.0/css/font-awesome.min.css');
@import url('https://fonts.googleapis.com/css2?family=Merriweather:ital,wght@0,700;1,700&family=Piazzolla:ital,wght@0,700;0,800;1,600;1,700&display=swap');

.item{
    border-radius: 10px;
    margin-bottom: 20px;
    background-image: linear-gradient(#ffffff,rgb(198, 198, 226));
    box-shadow: 3px 6px 8px #49639c;
    cursor: pointer;
}

.head-display{
    font-family: 'Piazzolla', serif;
    font-weight: 900;
    font-style: italic;
    font-size: 27px;
    color: rgb(21, 21, 94);
}

.image{
    width: 100%;
    height: 220px;
    border-radius: 20px;
    background-color: rgb(204, 201, 201);
    margin-left:5%;
    margin-top: 15px;
    margin-bottom: 2%;
    box-shadow: 1px 2px 3px black;
}

.row{
    margin-top: 10px;
}

.heads{
    font-family: 'Piazzolla', serif;
    font-weight: 700;
    margin-top: 15px;
    color: rgb(21, 21, 94);
    font-size: 30px;
}


.bottoms{
    font-family: 'Merriweather', serif;
    font-weight: 700;
    margin-top: 6px;
    font-size: 15px;
    color: rgb(38, 38, 99);
}

.hotel_name{
    margin-left: 5%;   
} 

.fa{
    color:rgb(38, 38, 99);
    margin-left: 5%;
    padding-top: 10px;
}

.cuisine_block {
    font-family: 'Merriweather', serif;
    font-size: 15px;
    color: #494c53;
    padding-left:10%;
    
}

.cuisine_out {
    font-family: 'Merriweather', serif;
    font-size: 15px;
    color: rgb(38, 38, 99);
}